/*
#3e301b
#547a46
#d6d0bc
*/

.feedBack-list-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.feedBack-list {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 35px;
  max-height: 500px;
  justify-content: center;
}

.feedBack-card {
  border: 2px solid #3e301b;
  background-color: #3e301b;
  box-shadow: 5px 5px 10px rgba(0, 0, 0.5);
  border-bottom: 6px solid #547a46;
  height: auto;
  width: 300px;
}

.feedBack-header {
  color: #d6d0bc;
  font-size: 1.3rem;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #3e301b;
}

.feedback-rating {
  font-size: 1rem;
  padding: 3px;
}

.comment {
  color: #d6d0bc;
  text-align: start;
  padding: 10px;
  font-size: 1.5rem;
  height: 150px;
  text-align: center;
  font-style: italic;
}

@media (max-width:768px) {

  .feedBack-list-container{
    width: 100%;
    box-sizing: border-box;
  }

    .feedBack-list{
      flex-wrap: nowrap;
      overflow-x: auto;
      max-width: 350px;
    }

    .feedBack-list::-webkit-scrollbar {
    display: none; 
  }
    .feedBack-list {
    -ms-overflow-style: none;  
    scrollbar-width: none; 
    justify-content: flex-start;
    margin: 15px;
  }

  .feedBack-card{
    flex-shrink: 0;
  }
}